'use client'

import Link from 'next/link'
import Image from 'next/image'

export default function Navbar() {
  return (
    <nav className="w-full px-6 py-4 flex items-center justify-between bg-white shadow-sm">
      {/* Kiri: Logo */}
      <div className="flex items-center gap-2">
        <Image
          src="/sarastya.jpg" // Ganti ke sarastya.png
          alt="Sarastya Logo"
          width={32}
          height={32}
          className="h-8 w-auto"
        />
        <span className="font-bold text-xl text-[#2A4C6F]">PT. Sarastya</span>
      </div>

      {/* Tengah: Menu navigasi */}
      <div className="hidden md:flex gap-6 text-[#2A4C6F] text-sm font-medium">
        <Link href="#">Beranda</Link>
        <Link href="#">Solusi</Link>
        <Link href="#">Harga</Link>
      </div>

      {/* Kanan: Login + Tombol */}
      <div className="flex items-center gap-4 text-sm">
        <Link href="#" className="text-[#2A4C6F]">Login</Link>
        <Link
          href="#"
          className="border border-[#2A4C6F] text-[#2A4C6F] px-4 py-1.5 rounded-full font-semibold hover:bg-[#2A4C6F] hover:text-white transition"
        >
          Demo Gratis
        </Link>
      </div>
    </nav>
  )
}
